import { getOwnPropertyDescriptor, MapCreate } from "../shared/utils";

const assignedSlotOriginal = getOwnPropertyDescriptor(Text.prototype, 'assignedSlot')!.get!;

const assignedSlotDistortion = function assignedSlot(): HTMLSlotElement | null {
    return null;
};

export default MapCreate([
    [assignedSlotOriginal, assignedSlotDistortion]
]); 
